import React, {useEffect, useState} from 'react';
import Cookies from "universal-cookie";
import axios from "axios";
import {TableBody, TableCell, TableRow, Table, Button, Box} from "@mui/material";

export const Status = ({setLoggedIn}) => {
    const cookies = new Cookies();
    const token = cookies.get('token');
    const [data, setData] = useState();
    const [isLoading, setIsLoading] = useState(false);

    const errorHandler = (error) => {
        if (error.response) {
            if(error.response.status === 401 || error.response.status === 403) {
                logOut();
            }
        } else {
            alert("  .   .")
        }
        setIsLoading(false);
    }

    useEffect(() => {
        if (!data) {
            setIsLoading(true);
            axios.get('http://34.194.225.209:8080/air_alarm/status', {
                headers: {
                    Authorization: `Bearer ${token}`
                }
            }).then(function (response) {
                setData(response.data);
                setIsLoading(false);
            }).catch(errorHandler)
        }
    }, [token, data])

    const startAlarm = () => {
        setIsLoading(true);
        axios.put(
            "http://34.194.225.209:8080/air_alarm/start",
            {},
            { headers: {Authorization: `Bearer ${token}`}}).then(function (response) {
                setTimeout(() => {
                    setData(null);
                    setIsLoading(false);
                }, 5000)
        }).catch(errorHandler)

    }
    const stopAlarm = () => {
        setIsLoading(true);
        axios.put(
            "http://34.194.225.209:8080/air_alarm/stop",
            {},
            { headers: {Authorization: `Bearer ${token}`}}).then(function (response) {
            setTimeout(() => {
                setData(null);
                setIsLoading(false);
            }, 5000)
        }).catch(errorHandler)
    }

    const logOut = () => {
        cookies.remove('token');
        setLoggedIn(false);
    }

    return data ? <Box>
        <Box sx={{display: 'flex', justifyContent: 'flex-end'}}>
            <Button onClick={logOut}></Button>
        </Box>
        <Table sx={{ opacity: isLoading ? 0.5 : 1}}>
            <TableBody>
                <TableRow>
                    <TableCell></TableCell>
                    <TableCell>{data.status === "STOPPED" ? "" : ""}</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell> </TableCell>
                    <TableCell>{data.alarm_changed_at}</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell>  Facebook</TableCell>
                    <TableCell>{data.facebook_published ? "Tak" : "Hi"}</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell>   Facebook</TableCell>
                    <TableCell>{data.facebook_published_at}</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell>  Telegram</TableCell>
                    <TableCell>{data.telegram_published ? "Tak" : "Hi"}</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell>   Telegram</TableCell>
                    <TableCell>{data.telegram_published_at}</TableCell>
                </TableRow>
            </TableBody>
        </Table>
        <Box sx={{mt: 2, ml: 2}}>
            <Button sx={{mr: 2}} color="success" variant="contained" disabled={isLoading} onClick={startAlarm}></Button>
            <Button color="error" variant="contained" disabled={isLoading} onClick={stopAlarm}></Button>
        </Box>
    </Box> : null;
}
